import { createRouter, createWebHashHistory } from 'vue-router'

const routes = [
  {
    path: '/',
    name: 'main',
    component: () => import('../views/Main')
  },
  {
    path: '/workExperience',
    name: 'workExperience',
    component: () => import('../views/WorkExperience')
  },
  {
    path: '/skill',
    name: 'skill',
    component: () => import('../views/Skill')
  },
  {
    path: '/credential',
    name: 'credential',
    component: () => import('../views/Credential')
  },  
];

const router = createRouter({
  history: createWebHashHistory(),
  routes
});

export { router }