<template>
  <nav>
      <ul>
        <li v-for="(item, index) in menuList" :key="index">
          <router-link :to="item.path">
            <i :class="['fas', `fa-${item.icon}`]"></i>
            <!-- <span>{{ item.name }}</span> -->
            <div class="round"></div>
          </router-link>
        </li>
        <li class="contact-menu" @click="visiblePop">
          <a>
            <i class="fas fa-phone-square"></i>
            <!-- <span>CONTACT</span> -->
            <div class="round"></div>
          </a>
        </li>
      </ul>
    </nav>

    <!-- popup -->
    <Contact ref="popupContact"></Contact>
</template>

<script>
import { ref } from 'vue';
import Contact from '@/views/Contact.vue';

export default {
  components: {
    Contact,
  },
  setup() {
    const menuList = [
      {
        name: 'HOME',
        icon: 'home',
        path: '/',
      },
      {
        name: '경력사항',
        icon: 'briefcase',
        path: '/workExperience',
      },
      {
        name: '자격&수료',
        icon: 'id-card',
        path: '/credential',
      },
      {
        name: '스킬',
        icon: 'laptop-code',
        path: '/skill',
      },
      // {
      //   name: 'PORTFOLIO',
      //   icon: 'th-list',
      //   path: '',
      // },
    ];

    const popupContact = ref(null);

    const visiblePop = () => {
      popupContact.value.popupOpen();
    };

    return {
      menuList,
      popupContact,
      visiblePop,
    };
  }
}
</script>
